<template>
  <div class="home">
    <div id="menubody">
        <div class="btn-group btn-group-justified" id="category" >
            <div class="btn-group">
                <button type="button" class="btn btn-light " @click="selectAll">All</button>
            </div>
            <div class="btn-group">
                <button type="button" class="btn btn-light" @click="selectVeg">Veg</button>
            </div>
            <div class="btn-group">
                <button type="button" class="btn btn-light" @click="selectNonVeg">Non Veg</button>
            </div>
        </div>
        <div class="card" v-for="(food, index) in all" :key="food.id">
            <div class="card-body">
                <div class="row">
                    <div class="col">
                        <h6 class="card-title">{{food.name}}</h6>
                        <p class="card-text">Price : Rs.{{food.price}}/-</p>
                    </div>
                    <div class="col" id="quantityCount">
                        <div class="btn-group" role="group" aria-label="Basic example">
                            <button type="button" class="btn bg-light border border-secondary" @click="removeFromCart(index)" :disabled="!food.availability">-</button>
                            <button type="button" disabled class="btn bg-light border border-secondary" id="quantityCountValue" >{{quantity[index]}}</button>
                            <button type="button" class="btn bg-light border border-secondary" @click="addToCart(index)" :disabled="!food.availability">+</button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
    <div id="placeOrder" class="bg-light border border-top">
        <router-link to="/home/ordersummary"><button class="btn btn-warning border-dark" @click="placeOrder">Place Order</button></router-link>
    </div>
  </div>
</template>

<script>
import service from '../service'
//import EventBus from '../eventBus'

export default {
  name: 'home',
  props: {
    msg: String
  },
  data() {
      return {
          all: [],
          foods: [],
          veg: [],
          nonVeg: [],
          category: 1,
          cart : [],
          quantity: []
      }
  },
  async created() {
          var obj = JSON.parse(JSON.stringify(await service.getFood()))
          this.foods = obj.data;
          for(var i=0; i<this.foods.length; i++) {
              this.quantity[i] = 0
              if(this.foods[i].category === "Veg")
                this.veg.push(this.foods[i])
              else if(this.foods[i].category === 'Non Veg')
                this.nonVeg.push(this.foods[i])
          }
        this.all = this.foods
  },
  methods: {
    async addOrder() {
      await service.insertUser()
    },
    selectAll() {
        this.category = 1
        this.all = this.foods
    },
    selectVeg() {
        this.category = 2
        this.all = this.veg
    },
    selectNonVeg() {
        this.category = 3
        this.all = this.nonVeg
    },
    addToCart(index) {
        this.cart.push(this.foods[index].id)
        this.quantity.splice(index, 1, (this.quantity[index] + 1))
        //await service.insertOrder(this.foods[index].id)
    },
    removeFromCart(index) {
        var id = this.foods[index].id
        for(var i = this.cart.length - 1; i >= 0; i--) {
                if (this.cart[i] === id) {
                   this.cart.splice(i, 1)
                }
        }
        this.quantity.splice(index, 1, (this.quantity[index] - 1))
    },
    async placeOrder() {
        //EventBus.$emit('placeOrder', this.cart);
        var obj = JSON.parse(JSON.stringify(this.cart))
        console.log(obj)
        await service.insertOrder(obj)
        //this.cart = []
    }
  }
}
</script>

