<template>
  <div class="cook">
    <div id="menubody">
        <div class="card" v-for="(food, index) in foods" :key="food.id">
            <div class="card-body">
                <div class="row">
                    <div class="col">
                        <h6 class="card-title">{{food.name}}</h6>
                        <p class="card-text">Price : Rs.{{food.price}}/-</p>
                    </div>
                    <div class="col" id="quantityCount">
                        <div class="btn-group" role="group">
                            <button type="button" class="btn bg-secondary " @click="removeFood(index)" :disabled="!food.availability">X</button>
                        </div>
                    </div>  
                </div>
            </div>
        </div>
    </div>
  </div>
</template>

<script>
import service from '../service'
//import EventBus from '../eventBus'

export default {
  name: 'cook',
  props: {
    msg: String
  },
  data() {
      return {
          foods: []
      }
  },
  async created() {
          var obj = JSON.parse(JSON.stringify(await service.getFood()))
          this.foods = obj.data;
  },
  methods: {
    async removeFood(index) {
        console.log(this.foods[index].id)
        var res = await service.removeFood(this.foods[index].id)
        console.log(this.foods)
        console.log(res)
    }
  }
}
</script>

