import axios from 'axios'

const url = "api/"

class service {
    //POST
    static insertUser(name, mobno, password) {
        return axios.post(`${url}signup/create/`, {
            name, mobno, password
        })
    }

    static getUser(id) {
        const res =  axios.get(`${url}signup/user/`, {
            params: {
              mobno: id
            }
          })
          console.log(res)
          return res
    }

    static getFood() {
        const res =  axios.get(`${url}foodlist/fooditems/`)
        console.log(res)
        return res
    }
    
    static insertOrder(cart) {
        return axios.post(`${url}order/create/`, {
            "id": cart
        }).then(res => {
            console.log(res)    
        })
    }

    static getOrder(tk) {
        const res =  axios.get(`${url}order/list/`, {
            params: {
                token: tk
              }
        })
        console.log(res)
        return res
    }

    static getRecentOrder() {
        const res =  axios.get(`${url}identitycounter/recent`)
        console.log(res)
        return res
    }

    static getAFood(id) {
        const res =  axios.get(`${url}foodlist/fooditem`, {
            params: {
                "id": id
              }
        })
        console.log(res)
        return res
    } 

    static removeFood(id) {
        console.log(id)
        const res = axios.put(`${url}foodlist/avail?id=${id}`, {
            "availability": false
        })
        console.log(res)
        return res
    }

    static getAllOrder() {
        const res =  axios.get(`${url}order/allorders`)
        console.log(res)
        return res
    }
}
export default service