<template>
  <div class="signup">
    <h1>Sign Up</h1>
    <form class="container titler" @submit.prevent="addUser">
      <div class="form-group">
        <label for="name">Name</label>
        <input type="text" class="form-control" id="name" v-model="name">
      </div>
      <div class="form-group">
        <label for="mobile">Mobile</label>
        <input type="number" class="form-control" id="mobile" v-model="mobno">
      </div>
      <div class="form-group">
        <label for="password">Password</label>
        <input type="password" class="form-control" id="password" v-model="password">
      </div>
      <router-link to='/'><button type="submit" class="btn btn-warning">SIGN UP</button></router-link>
    </form>
    <p>{{user}}</p>
  </div>
</template>

<script>
import service from '../service'

export default {
  name: 'signUp',
  props: {
    msg: String
  },
  data() {
    return {
        name: '',
        mobno: '',
        password: ''
    }
  },
  methods: {
    async addUser() {
      await service.insertUser(this.name, this.mobno, this.password)
    }
  }
}
</script>

