<template>
  <div class="OrderSummary">
    <br>
    <h3>Order Summary</h3>
    <hr>
    <p>Token no. : {{count}}</p>
    <div>
        <table class="table">
            <thead>
                <tr>
                <th scope="col">Item Ordered</th>
                <th scope="col">Quantity</th>
                <th scope="col">Price(in Rs.)</th>
                </tr>
            </thead>
            <tbody v-for="order in orders" :key="order[0].id">
                <tr>
                <th scope="row">{{order[0].name}}</th>
                <td>1</td>
                <td>{{order[0].price}}</td>
                </tr>
            </tbody>
        </table>
    </div>
    <div id="placeOrder" class="bg-light border border-top row">
        <div class="col">
            <p>Total Amount : Rs.{{sum}}</p>
        </div>
        
    </div>
  </div>
</template>

<script>
//import EventBus from '../eventBus'
import service from '../service'

export default {
  name: 'orderSummary',
  props: {
    msg: String
  },
  data() {
      return {
          fid: [],
          orders: [],
          count: 0,
          sum: 0
      }
  },
  async created() {
          var obj2 = JSON.parse(JSON.stringify(await service.getRecentOrder()))
          this.count = obj2.data[0].count
          var obj = JSON.parse(JSON.stringify(await service.getOrder(this.count)))
          this.fid = obj.data
          console.log(this.fid.id)
          for(var i = this.fid.id.length - 1; i >= 0; i--) {
              var id = this.fid.id[i]
              var obj3 = JSON.parse(JSON.stringify(await service.getAFood(id)))
              this.orders.push(obj3.data)
          }
          console.log(this.orders)
          for(var j = 0; j < this.orders.length; j++) {
              this.sum += this.orders[j][0].price
          }
  }
}
</script>

