<template>
  <div class="login">
    <h1>Login</h1>
    <form class="container titler" @submit.prevent="auth">
      <div class="form-group">
        <label for="mobile">Mobile</label>
        <input type="number" class="form-control" id="mobile" v-model="mobno">
      </div>
      <div class="form-group">
        <label for="password">Password</label>
        <input type="password" class="form-control" id="password" v-model="password">
      </div>
      <div class="row" id="loginNsignup">
      <router-link to="/home"><button type="submit" class="btn btn-warning border-dark">LOGIN</button></router-link>
      <router-link to="/signup" id="onlysignup"><button type="submit" class="btn btn-warning border-dark">SIGNUP</button></router-link>
      </div>
    </form>
    <p>{{valid}}</p>
  </div>
</template>

<script>
import service from '../service'

export default {
  name: 'login',
  props: {
    msg: String
  },
  data() {
      return {
          mobno: '',
          password: '',
          valid: 0,
          user: []
      }
    },
    methods: {
        async auth() {
            console.log(this.mobno)
            this.user = await service.getUser(this.mobno)
            var parsedobj = JSON.parse(JSON.stringify(this.user))
            console.log(parsedobj.data)
            if(parsedobj.data.password === this.password)
                this.valid = 1
        }
  }
}
</script>

