<template>
  <div class="manager">
    <h3 id="ordersTitle">Orders</h3>
    <div>
        <table class="table">
  <thead>
    <tr>
      <th scope="col">Token no.</th>
      <th scope="col">Total Amount</th>
    </tr>
  </thead>
  <tbody v-for="(order,index) in orders" :key="index">
    <tr>
      <td>{{order.token}}</td>
      <td>Rs.{{cost[index]}}</td>
    </tr>
  </tbody>
</table>
    </div>
  </div>
</template>

<script>
import service from '../service'

export default {
  name: 'manager',
  props: {
    msg: String
  },
  data() {
      return {
          orders: [],
          cost: []

      }
    },
    async created() {
          var obj = JSON.parse(JSON.stringify(await service.getAllOrder()))
          this.orders = obj.data
          this.dorders = obj.data
          console.log(this.orders)
          for(var i =0; i < this.orders.length; i++) {
              //console.log(this.orders[i].id)
              var sum = 0
              for(var j = this.orders[i].id.length - 1; j >= 0; j--){
                  var fid = this.orders[i].id[j]
                  //console.log(this.orders[i].id[j])
                  var obj2 = JSON.parse(JSON.stringify(await service.getAFood(fid)))
                  //console.log(obj2.data[0].price)
                  sum += obj2.data[0].price
              }
              //this.orders[i].sum = sum
              //this.cost[i] = sum
              this.cost.splice(i, 1, sum)
               //console.log(this.orders[i])
          }
          /*console.log(this.orders)
          for(var j = 0; j < this.orders.length; j++) {
              this.sum += this.orders[j][0].price
          }*/
    }
}
</script>

